.container-search{
    display: flex;
    flex-direction: column;
    text-align: left;
    
}

.search-bar{

    margin-top: 1.17rem;
    display: flex;
    flex-direction: column;
    
}


.button-alphabetic-order, .button-date-order, .button-reset-order{
    align-items: center;
    background-color: #FFFFFF;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: .25rem;
    box-shadow: rgba(0, 0, 0, 0.02) 0 1px 3px 0;
    box-sizing: border-box;
    color: rgba(0, 0, 0, 0.85);
    cursor: pointer;
    display: inline-flex;
    font-family: system-ui,-apple-system,system-ui,"Helvetica Neue",Helvetica,Arial,sans-serif;
    font-size: 16px;
    font-weight: 600;
    justify-content: center;
    line-height: 1.25;
    margin: 0;
    min-height: 3rem;
    padding: calc(.875rem - 1px) calc(1.5rem - 1px);
    position: relative;
    text-decoration: none;
    transition: all 250ms;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    vertical-align: baseline;
    width: auto;

}

.button-alphabetic-order:hover,
.button-alphabetic-order:focus,
.button-date-order:hover,
.button-date-order:focus,
.button-reset-order:hover,
.button-reset-order:focus
{
  border-color: rgba(0, 0, 0, 0.15);
  box-shadow: rgba(0, 0, 0, 0.1) 0 4px 12px;
  color: rgb(245, 201, 7);
}


.button-alphabetic-order:hover,
.button-date-order:hover,
.button-reset-order:hover{
    transform: translateY(-1px);
  }


  .button-alphabetic-order:active,
  .button-date-order:active,
  .button-reset-order:active {
    background-color: #F0F0F1;
    border-color: rgba(0, 0, 0, 0.15);
    box-shadow: rgba(0, 0, 0, 0.06) 0 2px 4px;
    color: rgba(0, 0, 0, 0.65);
    transform: translateY(0);
  }

  .budget-sheet-box{
    overflow: auto;
    height: 400px;
    background-color: rgb(255, 255, 255, 0.8);
    opacity: 0.8;
    border-radius: 15px;
  }

  .user-sheet{
    border: 2px solid white;
    
    border-radius: 20px;
    background-color: rgb(245, 201, 0.8);
    color: black;
    margin: 0.7rem;
    padding-left: 0.7rem;
    
  }
  